import React, { Component } from "react";
import Stepper from "@material-ui/core/Stepper";
import Step from "@material-ui/core/Step";
import { withRouter } from "react-router-dom";
import StepLabel from "@material-ui/core/StepLabel";
import Typography from "@material-ui/core/Typography";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import * as Actions from "../../store/User/UserAction";
import Button from "@material-ui/core/Button";
import "../../styles/instructions.css";
import Step1 from "./step1";
import Step2 from "./step2";
import Step3 from "./step3";
import birballogo from "../../assets/images/instructions/birballogo.png";
import ArrowForwardIcon from "@material-ui/icons/ArrowForward";
import ReactGA from "react-ga";
// import Analytics from 'analytics'
// import googleAnalytics from '@analytics/google-analytics';

function getSteps() {
  return ["1", "2", "3"];
}

export class Instructions extends Component {
  constructor(props) {
    super(props);

    this.state = {
      activeStep: 0,
      loaddd: false,
      step: 0,
      enable: false,
      loginDetails: "",
      responseId: localStorage.getItem("responseId"),
    };
  }

  componentDidMount() {
    // this.analytics.page()
    ReactGA.pageview(window.location.pathname);
  }

  // analytics = Analytics({
  //   app: 'birbal-candidate',
  //   plugins: [
  //     googleAnalytics({
  //       trackingId: 'UA-58884491-1'
  //     })
  //   ]
  // })

  setActiveStep(e) {
    this.setState({ activeStep: e });
  }

  changeHandler() {
    this.setState({ loaddd: !this.state.loaddd });
  }

  handleBack = async () => {
    await this.setState({ activeStep: this.state.activeStep - 1 });
    const { responseId } = this.state;
    responseId &&
      this.props.UserAction.addEvent({
        responseId: responseId,
        event: {
          type: "debug",
          level: 3,
          metadata: { Page: "Instruction", step: this.state.activeStep },
          timestamp: new Date().toISOString(),
        },
      });
  };

  handleNext = async () => {
    await this.setState({ activeStep: this.state.activeStep + 1 });
    const { responseId } = this.state;
    responseId &&
      this.props.UserAction.addEvent({
        responseId: responseId,
        event: {
          type: "debug",
          level: 3,
          metadata: { Page: "Instruction", step: this.state.activeStep + 1 },
          timestamp: new Date().toISOString(),
        },
      });
  };

  handleReset = () => {
    this.setActiveStep(0);
  };

  handlePreview = () => {
    const { responseId } = this.state;
    responseId &&
      this.props.UserAction.addEvent({
        responseId: responseId,
        event: {
          type: "debug",
          level: 3,
          metadata: { Page: "Preview" },
          timestamp: new Date().toISOString(),
        },
      });
    this.props.history.push({
      pathname: "/preview",
      state:
        this.props &&
        this.props.history &&
        this.props.history.location &&
        this.props.history.location.state,
    });
  };

  handleStep = (step) => () => {
    if (step < this.state.activeStep) {
      this.setState({ activeStep: step });
    }
  };

  getStepContent(stepIndex) {
    switch (stepIndex) {
      case 0:
        return (
          <Step1
            data={
              this.props &&
              this.props.history &&
              this.props.history.location &&
              this.props.history.location.state
            }
          />
        );
      case 1:
        return (
          <Step2
            data={
              this.props &&
              this.props.history &&
              this.props.history.location &&
              this.props.history.location.state
            }
          />
        );
      case 2:
        return (
          <Step3
            data={
              this.props &&
              this.props.history &&
              this.props.history.location &&
              this.props.history.location.state
            }
          />
        );
      // case 3:
      // 	return <Step4 />;
      default:
        return "Unknown stepIndex";
    }
  }

  render() {
    const steps = getSteps();

    return (
      <div>
        <div className='instruction-main-div'>
          <div className='main-wrapper'>
            <div className='left-content'>
              <div className='instructions-heading'>
                <Typography>Basic Instructions</Typography>
              </div>
              <div className='instructions-subHeading'>
                <Typography>Read and follow the given Instructions</Typography>
              </div>
            </div>

            <div className='right-content'>
              <div className='instructions-logo'>
                <img src={birballogo}></img>
              </div>
            </div>
          </div>

          <div className='page-content'>
            <div className='mainstepper'>
              <div>
                <Stepper activeStep={this.state.activeStep} alternativeLabel>
                  {steps.map((label, index) => (
                    <Step key={label} onClick={this.handleStep(index)}>
                      <StepLabel
                        iconContainerStyle={{ width: 72 }}
                        className='step-label'
                      >
                        {label}
                      </StepLabel>
                    </Step>
                  ))}
                </Stepper>
              </div>
              <div>
                {this.state.activeStep === steps.length - 1 ? (
                  <Button
                    variant='contained'
                    className='Button1 '
                    onClick={this.handlePreview}
                  >
                    Go to Preview
                  </Button>
                ) : (
                  <Button
                    variant='contained'
                    className='Button '
                    onClick={this.handleNext}
                  >
                    <span></span>
                    <span>Next</span>
                    <span>
                      {" "}
                      <ArrowForwardIcon className='arrowChanges'></ArrowForwardIcon>
                    </span>
                  </Button>
                )}
              </div>
            </div>
            <div className='contents'>
              <div className='mainstepper-mobile'>
                <Stepper activeStep={this.state.activeStep} alternativeLabel>
                  {steps.map((label, index) => (
                    <Step key={label} onClick={this.handleStep(index)}>
                      <StepLabel
                        iconContainerStyle={{ width: 72 }}
                        className='step-label'
                      >
                        {label}
                      </StepLabel>
                    </Step>
                  ))}
                </Stepper>
              </div>

              {this.state.activeStep === steps.length ? null : (
                <div>{this.getStepContent(this.state.activeStep)}</div>
              )}
            </div>
          </div>
        </div>
        <div className='button-new'>
          {this.state.activeStep === steps.length - 1 ? (
            <Button
              variant='contained'
              className='Button1 '
              onClick={this.handlePreview}
            >
              Go to Preview
            </Button>
          ) : (
            <Button
              variant='contained'
              className='Button'
              onClick={this.handleNext}
            >
              <span></span>
              <span>Next</span>
              <span>
                {" "}
                <ArrowForwardIcon className='arrowChanges'></ArrowForwardIcon>
              </span>
            </Button>
          )}
        </div>
      </div>
    );
  }
}
const mapStateToProps = (state) => {
  return {
    UserReducer: state.UserReducer,
  };
};
const mapDispatchToProps = (dispatch) => {
  return {
    UserAction: bindActionCreators(Actions, dispatch),
  };
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(Instructions));
