import React from "react";

import "../../styles/instructions.css";

const Step1 = (props) => {
  const data = props && props.data;
  const verbal =
    data &&
    data.response &&
    data.response.questionType.find((el) => el.name.toLowerCase() === "verbal");
  const mcq =
    data &&
    data.response &&
    data.response.questionType.find((el) => el.name.toLowerCase() === "mcq");
  const code =
    data &&
    data.response &&
    data.response.questionType.find((el) => el.name.toLowerCase() === "coding");
  return (
    <div>
      <ul>
        <li className='li'>
          Please ensure that you are on a laptop or desktop.
        </li>
        <li className='li'>
          Once the video interview starts, do not refresh the page. If you do,
          your progress will be lost and you will be unable to take the
          interview again.
        </li>
        <li className='li'>
          Please ensure all entered details are correct. You will not be
          permitted to modify them later.
        </li>
        <li className='li'>
          The following will comprise of{" "}
          {data && data.response && data.response.questionType.length} section.
        </li>
        {verbal ? (
          <li className='li'>
            The Verbal section will comprise of{" "}
            {data && data.response && data.response.verbalQuestions.length}{" "}
            questions. You will get 10 seconds to read the question.
          </li>
        ) : null}
        {mcq &&
        data &&
        data.response &&
        data.response.mcqQuestions.length > 0 ? (
          <li className='li'>
            The MCQ section will comprise of {data.response.mcqQuestions.length}{" "}
            questions. Choose the correct option for each.
          </li>
        ) : null}
        {code &&
        data &&
        data.response &&
        data.response.codingQuestions.length > 0 ? (
          <li className='li'>
            The Coding section will comprise of{" "}
            {data.response.codingQuestions.length} questions. You can choose any
            language option give to code.
          </li>
        ) : null}
      </ul>
    </div>
  );
};

export default Step1;
