import React from "react";
import "../../styles/instructions.css";
const Step3 = () => {
  return (
    <div>
      <ul>
        <li className="li">
          Please ensure that the audio quality of your laptop/desktop is good.
        </li>
        <li className="li">
          Test your Laptop’s webcam. It’s mandatory to keep it on during the
          video interview.
        </li>
        <li className="li">
          Position your webcam so that you have a neutral background that’s free
          from distractions.
        </li>
        <li className="li">
          Check your headphones before using them. Your mic should be working.
        </li>
        <li className="li">
          Close any unnecessary web browser tabs and applications before
          starting the interview.
        </li>
      </ul>
    </div>
  );
};

export default Step3;
